@Component({
  name: 'live-index',
  components: {
    EmployAliPlayer,
    EmployVideojsPlayer,
    CommentDanmu,
    CreateDanmu,
  },
})
export default class LiveIndex extends Vue {
  /* ------------------------ INPUT & OUTPUT ------------------------ */

  /* ------------------------ VUEX (vuex getter & vuex action) ------------------------ */

  /* ------------------------ LIFECYCLE HOOKS (created & mounted & ...) ------------------------ */
  private mounted() {
    this.makeInterVal = setInterval(() => {
      this.get_player_current_time();
    }, 1000);
  }
  private destroyed() {
    this._clearInterVal();
  }
  /* ------------------------ COMPONENT STATE (data & computed & model) ------------------------ */
